@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: "Bree Serif", serif;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header-container{max-width:620px;display:flex;justify-content:center;align-items:center;margin:0 auto;padding:15px 0}.header-icon-container{width:60%;display:flex;align-items:center}.header-icon-container img{width:35px;height:auto;margin-right:5px}.header-icon-container span{font-size:1.4rem}.header-setting-container{width:40%;display:flex;align-items:center;justify-content:flex-end}.header-setting-container button{margin-right:10px;padding:8px 15px;border-radius:10px;font-family:"Bree Serif",serif;cursor:pointer;border-width:1px 1px 2px 1px;background-color:#fff}
.clock-page{min-height:650px;color:#fff;font-family:"Lato",sans-serif}
.clock-panel-container{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;max-width:580px;margin:0 auto}.clock-panel{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;background-color:rgba(255,255,255,.1);margin-top:50px;width:100%;border-radius:10px;padding:40px 0}.clock-header-container button{margin-right:15px;font-size:1.2rem;color:#fff;background-color:transparent;border-style:none;border-radius:8px;padding:5px 15px;cursor:pointer}.clock-header-container :focus{background-color:rgba(0,0,0,.1)}.clock-timer{font-size:8rem;margin:20px 0}.clock-start-button{min-width:250px;padding:10px 20px;border-radius:10px;cursor:pointer;border-style:none;font-size:2rem;font-weight:600;box-shadow:#c8c8c8 0px 6px 0px}.clock-stop-button{font-size:2rem;cursor:pointer;position:relative;left:20px}
.task-panel-setting{width:200px;background-color:#fff;color:#000;position:absolute;top:50px;left:350px;border-radius:10px;padding:10px}.task-panel-setting :hover{background-color:#e2e1e1}.task-panel-setting .setting-item{padding:10px;cursor:pointer}.task-panel-setting .setting-item i{margin-right:5px}
.task-lists-container{width:100%;list-style:none;padding:0}
li{padding:10px;background:#fff;color:#000;margin-bottom:10px;border-radius:8px;display:flex;flex-wrap:wrap}li .task-item-title{width:92%;display:flex;align-items:center}li .task-item-title .title{font-size:1.1rem;font-weight:600}li .task-item-title .check-icon{margin-right:10px}li .trash-icon{font-size:1.2rem;cursor:pointer}li .note{background-color:#faf4d1;width:80%;padding:10px;margin:10px auto}
.add-task-form{background-color:#fff;color:#000;border-radius:10px;margin-bottom:20px}.add-task-form .add-task-form-content{padding:10px 20px}.add-task-form input[type=text]{display:block;width:90%;font-size:1.5rem;border:none;margin:20px 0}.add-task-form .add-note-button{cursor:pointer;text-decoration:underline;margin-bottom:10px}.add-task-form .add-note-textarea{width:90%;min-height:50px;background-color:#e2e1e1;border:none;margin-bottom:10px}.save-cancel-container{display:flex;justify-content:flex-end;background-color:#e2e1e1;padding:15px;border-bottom-left-radius:10px;border-bottom-right-radius:10px}.save-cancel-container button{margin-right:5px;padding:8px 12px;font-weight:600;border:none;background-color:transparent;cursor:pointer}
.task-panel{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;max-width:550px;margin:30px auto;position:relative}.task-header-container{width:100%;display:flex;justify-content:space-between;font-size:1.3rem;font-weight:600;padding:10px 0;border:1px solid #fff;border-width:0 0 1px 0}.task-header-container i{background-color:rgba(255,255,255,.2);padding:5px 10px;border-radius:8px;cursor:pointer}.task-form-container{width:100%}.add-task-button-container{text-align:center;background-color:rgba(0,0,0,.2);padding:20px;margin-top:20px;border:dotted 1px #fff;border-radius:10px;cursor:pointer;margin-bottom:20px}.add-task-button-container i{margin-right:5px}
.document-page{width:620px;margin:20px auto}li{padding:0}
.footer{text-align:center;border-width:1px 0 0 0;border-top:1px solid rgba(0,0,0,.253);padding:10px 0}.link{color:#f81a0ad0}a{color:#000;text-decoration:none}.github{font-size:1.5rem}
